import formatTime from '../helpers/format-time'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('VideoSeekbar', component.exports)
}
export default {
    name: 'VideoSeekbar',
    props: {
        time: {
            type: Number
        },
        buffered: {
            type: Number
        },
        duration: {
            type: Number
        }
    },
    data: () => (
        {
            hoveredFraction: -1,
            dragging: false,
            boundMouseUp: null,
            boundMouseMove: null
        }
    ),
    computed: {
        currentTime: {
            get() {
                return this.time
            },
            set(val) {
                this.$emit('update:time', val)
            }
        }
    },
    methods: {
        rect() {
            return this.$el.getBoundingClientRect()
        },
        mouseDown(e) {
            this.dragging = true
            if (!this.boundMouseUp) {
                this.boundMouseMove = this.handleMove.bind(this)
                this.boundMouseUp = this.mouseUp.bind(this)
            }
            document.addEventListener('mousemove', this.boundMouseMove)
            document.addEventListener('touchmove', this.boundMouseMove, { passive: false })
            document.addEventListener('mouseup', this.boundMouseUp)
            document.addEventListener('touchend', this.boundMouseUp, { passive: false })
            this.handleMove(e)
            this.$emit('dragstarted')
        },
        mouseUp(e) {
            this.dragging = false
            document.removeEventListener('mousemove', this.boundMouseMove)
            document.removeEventListener('touchmove', this.boundMouseMove)
            document.removeEventListener('mouseup', this.boundMouseUp)
            document.removeEventListener('touchend', this.boundMouseUp)
            this.$emit('dragended')
        },
        updateHover(e) {
            this.hoveredFraction = Math.min(1, Math.max(0, (
                e.clientX - this.rect().left
            ) / this.rect().width))
        },
        mouseOut(e) {
            this.hoveredFraction = -1
        },
        handleMove(e) {
            if (!this.dragging) { return }
            let x
            if (e instanceof MouseEvent) {
                x = e.clientX
            } else {
                x = e.changedTouches[0].clientX
            }
            let frac = (
                x - this.rect().left
            ) / this.rect().width
            if (frac < 0) {
                frac = 0
            }
            if (frac > 1) {
                frac = 1
            }
            const secs = frac * this.duration
            this.$emit('dragged', secs)
            e.preventDefault()
        },
        formatTime(secs, guide = this.duration) {
            return formatTime(secs, guide)
        }
    }
}
